/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useMemo, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import useYupValidationResolver from '../../hooks/useYupValidationResolver';
import { useDispatch, useSelector } from 'react-redux';
import { Row, Col, Card, CardTitle, CardBody, Form, Button } from 'reactstrap';
import Dropdown from '../../components/custom/dropdown';

import {
  listagemClienteRequest,
  alterarStatusClienteRequest,
} from '../../store/modules/clientes/actions';
import Pagination from 'react-js-pagination';
import { InputMask } from '../../components/custom/Inputs';

const Clientes = () => {
  // Validao
  let validationSchema = useMemo(
    () =>
      yup.object({
        type: '',
        cpf: '',
      }),
    []
  );

  const resolver = useYupValidationResolver(validationSchema);
  const { handleSubmit, register } = useForm({ resolver });
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.cliente.loading);
  const listagem = useSelector((state) => state.cliente.listagemClientes);

  const listagemClientes = (data) => {
    const dados ={
      ...data,
      numeroPagina: 1
    }
    dispatch(listagemClienteRequest(dados));
  };

  useEffect(() => {
    const data = {
      type: 'Analista Senior',
      numeroPagina: 1,
    };
    dispatch(listagemClienteRequest(data));
  }, [dispatch]);

  const realizarPaginacao = (numeroPagina) => {
    dispatch(listagemClienteRequest(numeroPagina));
  };

  const ativarDesativarCliente = (id) => {
    const response = window.confirm('Voc desaja alterar o status?');
    if (response) {
      dispatch(alterarStatusClienteRequest(id));
    }
  };

  return (
    <>
      <div className="row">
        <Col sm="12" lg="12">
          <Card className="iq-card">
            <div className="iq-card-header d-flex justify-content-between">
              <CardTitle className="card-title">
                <h4>Listagem de clientes</h4>
              </CardTitle>

              <Link
                className="btn btn-primary float-right"
                to="/cadastro-clientes"
              >
                Cadastrar
              </Link>
            </div>
            <Form onSubmit={handleSubmit(listagemClientes)}>
              {loading ? (
                <img
                  width="100"
                  height="100"
                  src={require('../../assets/images/loading.gif')}
                  alt="Carregando..."
                />
              ) : (
                <CardBody className="iq-card-body">
                  <Row className="mb-4">
                  <Col sm="12" md="6" lg="2">
                        <InputMask type="text" placeholder="CPF" name="cpf"  mask="999.999.999-99" ref={register} className="form-control" />
                      </Col>

                    <Col sm="12" md="6" lg="4">
                      <button
                        type="submit"
                        color="primary"
                        className="btn btn-primary btn-lg"
                      >
                        {loading ? 'Carregando...' : 'Pesquisar'}
                      </button>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12">
                      <table className="table">
                        <thead>
                          <tr>
                            <th scope="col">ID</th>
                            <th scope="col">Nome</th>
                            <th scope="col">CPF</th>
                            <th scope="col">Email</th>
                            <th scope="col">Celular</th>
                            <th scope="col">Status</th>
                            {/* <th scope="col">Cargo</th> */}

                            <th scope="col"></th>
                          </tr>
                        </thead>
                        <tbody>
                          {listagem &&
                            listagem.data.map((cliente, index) => (
                              <tr key={index}>
                                <th scope="row">{cliente.id}</th>
                                <td>{cliente.name}</td>
                                <td>{cliente.cpf}</td>
                                <td>{cliente.email}</td>
                                <td>{cliente.celular}</td>
                                <td>
                                  {!!cliente.ativo ? (
                                    <Button
                                      color="success"
                                      size="sm"
                                      id="ativarCliente"
                                      onClick={() =>
                                        ativarDesativarCliente(cliente.id)
                                      }
                                    >
                                      Ativado
                                    </Button>
                                  ) : (
                                    <Button
                                      type="button"
                                      color="danger"
                                      size="sm"
                                      id="desativarCliente"
                                      onClick={() =>
                                        ativarDesativarCliente(cliente.id)
                                      }
                                    >
                                      Desativado
                                    </Button>
                                  )}
                                </td>
                                {/* <td>{cliente.roles[0].name}</td> */}

                                <td>
                                  <Dropdown clienteId={cliente.id} />
                                </td>
                              </tr>
                            ))}
                        </tbody>
                      </table>
                      {listagem && (
                        <Pagination
                          activePage={listagem.current_page}
                          totalItemsCount={listagem.total}
                          itemsCountPerPage={listagem.per_page}
                          onChange={(numeroPagina) =>
                            realizarPaginacao(numeroPagina)
                          }
                          itemClass="page-item"
                          linkClass="page-link"
                          firstPageText="Incio"
                          lastPageText="Fim"
                        />
                      )}
                    </Col>
                  </Row>
                </CardBody>
              )}
            </Form>
          </Card>
        </Col>
      </div>
    </>
  );
};

export default Clientes;
